import React, {useState, useEffect} from 'react'
import { Link } from 'react-router-dom'
import '../../src/App.css';

function ItemLists(props) {
    const [wishlist, setWishlist] = useState([])

  const getWishlist = async () => {
    try {
      const wishlist = await fetch("http://localhost:9000/wishlist");
      const parsedWishlist = await wishlist.json();
      setWishlist(parsedWishlist);
    } catch (err) {
      console.log(err);
    }
  };
  

  useEffect(() => {
    getWishlist();
  }, []);
    return (
        <>
        <div className="wrapper">
            
          {wishlist && wishlist.map(item => (
            <div className ="card red" key={item._id} >
            <h2>{item.name}</h2> 
            <p> {item.recipient} </p>
            <p>{item.occation} {item.price} {item.isbought  ? "True" : "False"}</p>
            </div>
          ))}
       
        </div>
        <div>
            <Link to="/wishlist/new">Create New Holiday</Link>
        </div>
        </>
    )
}

export default ItemLists
